/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-hide-or-toggle-simple-toggle {
	border:0;      		
	text-align: left;
	
	&.is-style-default{
		padding: 1em;
		margin: 1em;	
	}

	&.is-style-expanded{
		background-color: azure;
		border-radius: 1px;
		padding: 1.5em;
		margin: 1em;	
	}
	&.is-style-compact{
		background-color: whitesmoke;
		border-radius: 5px;
		padding: 1px;
		margin: 1px;
	
	}
}

.wp-block-hide-or-toggle-simple-toggle .title{
	background-color: inherit; 
	color: inherit;
	margin-right: 1em;
	margin-top:1px;	
	border-style: solid;
	border-width: 1px;
	border-radius: 1px;
	cursor: pointer;
}

.wp-block-hide-or-toggle-simple-toggle .hidden  {
	background-color: inherit; 
	color: inherit;
	margin-left: 1em;
	margin-right: 1em;
	margin-top:1px;	
	border-style: solid;
	border-width: 1px;
	border-radius: 1px;
	cursor: pointer;
	display: none;
}
